@import url(https://fonts.googleapis.com/css?family=Oswald|Press+Start+2P);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;text-decoration:none;list-style:none;margin:0 auto;padding:0;text-align:center;font-family:"Oswald"}html{font-size:62.5%;position:relative;height:100%;scroll-behavior:smooth}body{width:100vw;overflow-x:hidden;display:flex;align-content:center;align-items:center;flex-flow:wrap column;justify-content:center;min-height:100%}button{color:white}h1{font-family:"Press Start 2P";font-size:4rem;margin-bottom:5rem}h2{font-family:"Press Start 2P";font-size:3rem;margin-bottom:3rem}h3{font-family:"Oswald";font-size:2.8rem;line-height:150%}p,label,input{color:#1b1a1a;font-size:2rem;line-height:1.8}i{font-size:3rem}i.fa-arrow-down{color:white;font-size:5rem;padding:1rem;background-color:#0077b6;border:6px solid #0077b6;border-radius:50%;transition:all 0.5s ease-out;animation:appear 3s forwards}span{color:#0077b6}ul{align-items:center}a{cursor:pointer;color:white}img{cursor:pointer}.hide{display:none !important}@keyframes pulse{0%{background-color:inherit}100%{background-color:#fff}}@keyframes appear{0%{opacity:0}100%{opacity:1}}.loading-animation{width:100vw;height:100vh;background-color:#f2f2f5}.loading{display:inline-block;position:relative;top:50%;width:8rem;height:8rem}.loading:after{content:" ";display:block;border-radius:50%;width:0;height:0;margin:0.8rem;box-sizing:border-box;border:3.2rem solid #000;border-color:#0077b6 transparent #0077b6 transparent;animation:lds-hourglass 1.2s infinite}@keyframes lds-hourglass{0%{transform:rotate(0);animation-timing-function:cubic-bezier(0.55, 0.055, 0.675, 0.19)}50%{transform:rotate(900deg);animation-timing-function:cubic-bezier(0.215, 0.61, 0.355, 1)}100%{transform:rotate(1800deg)}}header{width:100%}nav{min-height:10vh;display:flex;align-content:center;align-items:center;flex-flow:wrap row;position:-webkit-sticky;position:sticky;top:0;background-color:#0077b6}.flags{display:flex;align-content:center;width:100%}main{background-color:#fff;display:flex;align-content:center;align-items:center;flex-flow:wrap column;justify-content:space-around;width:90vw;min-height:100vh}main>:first-child{margin:5rem 0}.exercises>article{background-color:#f2f2f5;display:flex;align-content:center;align-items:center;flex-flow:wrap column;justify-content:space-evenly;margin:2rem 0;width:80vw;min-height:75vh;border-radius:2rem;box-shadow:inset 5rem 5rem 10rem #d9d9d9}.exercises>article>:first-child{margin:2rem 0}.exercises>:nth-child(even){background-color:#d9d9d9}.text-input{position:relative;margin:10rem 0}.text-input input[type="text"]{display:inline-block;width:70vw;height:8rem;outline:none;border:1px solid lightgray;border-radius:3px;padding:1rem 1rem 1rem 10rem;transition:all 0.5s ease-out}.text-input input[type="text"]+label{position:absolute;top:0;left:0;bottom:0;height:8rem;line-height:8rem;color:white;border-radius:5px;padding:0 2rem;background:#0077b6;transform:translateZ(0) translateX(0);transition:all 0.5s ease-in;transition-delay:0.2s}.text-input input[type="text"]:focus+label{transform:translateY(-120%) translateX(0%);border-radius:3px;transition:all 0.5s ease-out}.text-input input[type="text"]:focus{padding:1rem;transition:all 0.3s ease-out;transition-delay:0.2s}.text-output{font-weight:bold;display:flex;align-content:center;align-items:center;flex-flow:wrap column;justify-content:center;margin:5rem 0;min-width:45vw;min-height:30vh;border-radius:2rem;overflow-wrap:break-word;max-width:60vw;overflow:hidden;animation:pulse 3s forwards;transition:all 0.5s ease-out}.text-output h3{max-width:95%}@media only screen and (min-width: 768px) and (min-width: 1024px){.text-input input[type="text"]{width:40vw}}footer{display:flex;align-content:center;align-items:center;flex-flow:wrap column;justify-content:space-around;min-height:20vh;width:100%;background-color:#1b1a1a;color:white}address{display:flex;align-content:center;align-items:center;flex-flow:wrap row;width:100%;justify-content:space-evenly}address i{font-size:3.5rem}address a{transition:all 0.2s ease-in}address a:hover{transform:scale(1.2)}address a:hover i{color:#f2f2f5}.made-with-react{display:flex;align-content:center;align-items:center;flex-flow:wrap row;transform:translateY(50%)}.made-with-react p{color:white;margin:0 auto}.made-with-react i{animation:rotating 2s linear infinite;font-size:2.5rem;background-color:transparent}small{font-size:large}svg{background-color:#f2f2f5;margin-bottom:-1rem}@media only screen and (min-width: 768px){address{width:50%}address i{font-size:4rem}}@media only screen and (min-width: 768px) and (min-width: 1024px){address i{font-size:5rem}}@media only screen and (min-width: 768px){h1{font-size:5rem}h2{font-size:3rem}}

